import React, {useState} from 'react';
import { NavLink } from 'react-router-dom';
import './Navigation.scss';
import Cursor from '../cursor/Cursor';

const navItems = [
    { name: "about us", path: "/about", exact: true },
    { name: "tastes", path: "/tastes" },
    { name: "contact", path: "/contact" }
  ]

const Navigation = (props)=>{
    const [cursorPointer, setCursorPointer] = useState(false);
    


    const navigation = navItems.map(item=>(
        <NavLink
        onMouseEnter={() => setCursorPointer(true)}
        onMouseLeave={() => setCursorPointer(false)}
        key={item.name}
        to={item.path}
        exact={item.exact ? item.exact : false}
        >
            {item.name}
        </NavLink>
    ));
    
    return(
        <>
        <Cursor cursorClass={cursorPointer? 'active': null}/>
        <nav className={`navigation ${props.navClass}`}>
            <div className="navigation_container">{navigation}</div>
        </nav>
        </>
        
    )

}

export default Navigation