import React from 'react';
import './Taste.scss'

const Taste = (props)=>{

    const tastes= props.tastes.map((item)=>(
        <figure>
            <img src={item.src} alt="nacho_cheese_pic"/>
            <figcaption>
                <div>
                    <h4>{item.name}</h4>
                    <p>{item.description}</p>
                </div>
            </figcaption>
        </figure>   
          
        ))
    return(
        <div className="tastes_container">
            {tastes}
        </div>
    )

}

export default Taste