import React, { useState} from 'react';
import emailjs from 'emailjs-com';
import "./Form.scss";
import { useHistory } from "react-router-dom";

const Form = ()=>{
    let history = useHistory();


    const [initialState, setmyState] = useState({
        username: undefined,
        email: undefined,
        textarea: undefined
    });

    const onChange=(e)=>{
        e.preventDefault()
        setmyState({name: e.target.name, value: e.target.value})
    }

    const onSubmit=(e)=>{
        e.preventDefault()
        history.push('/thankyou');

        emailjs.sendForm('gmail', 'template_Ay6KFbNn', e.target, "user_dpjpnQw0ZGN41a3ZeW68T") //Insert your email service ID and email template ID
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
    
    }

    const {username, email, textarea} = initialState

    return(
        <form className="contactForm" onSubmit={onSubmit}>
           <label htmlFor="name">Your name</label>
           <input className="contactForm_name" type="text" name="username" value={username} onChange={onChange}/>
           <label htmlFor="email">Your email:</label>
           <input className="contactForm_email" type="email" name="email" value={email} onChange={onChange}/> 
           <label htmlFor="textarea">Your massage:</label>
           <textarea className="contactForm_message" name="textarea" cols="30" rows="10"value={textarea} onChange={onChange}/> 
           <input type="submit" value="Submit" className="contactForm_button" />
        
        </form>
    )
}

export default Form