import React, {useState} from 'react';
import "./Contact.scss";
import Form from "../components/form/Form"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookSquare } from "@fortawesome/free-brands-svg-icons";
import { faInstagram } from "@fortawesome/free-brands-svg-icons";
import HiddenNavigation from '../components/navigation/HiddenNavigation';
import Cursor from '../components/cursor/Cursor';
import logo from '../images/hajjp_logo.png'

const Contact = ()=>{

    const [cursorPointer, setCursorPointer] = useState(false);

    return(
        <section className="contact">
            <Cursor
                cursorClass={
                cursorPointer ? "coral-color-cursor active" : "coral-color-cursor"
                }
            />
             <div
            onMouseEnter={() => setCursorPointer(true)}
            onMouseLeave={() => setCursorPointer(false)}
            >
                 <HiddenNavigation/>
            </div>

            <div className="contact_container">
            
                <div className="contact_socialmedia">
                    <a href="https://facebook.com">  <FontAwesomeIcon icon={faFacebookSquare} /></a>
                    <h1 >Feel free to contact us</h1>
                    <a href="https://instagram.com">  <FontAwesomeIcon icon={faInstagram} /></a>
                </div>

                <div className="contact_form-container">
                    <Form/>
                </div>
            </div>

            <div className="contact_logo">
                <a href="/"><img src={logo} alt="logo"/></a>
            </div>
            

        </section>
    )

}

export default Contact